.modal-page {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    bottom: 116rem;
}

.modal-page-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
}

.modal-hide {
    top: 100vh;
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    background: #ffffff;
}

.modal-show {
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    top: 0;
    background: #ffffff;
}



